/**
 *  
 * @param {Function} cb -  
 * @param {Number} interval -  
 * @return {Function} -    
 */
export default function debounce(cb, interval = 500) {
  let lastTimeout = null;

  return function (...parameters) {
    if (lastTimeout) {
      window.clearTimeout(lastTimeout);
    }
    lastTimeout = window.setTimeout(function () {
      cb(...parameters);
    }, interval);
  };
}
